import React, { useContext,useState, useEffect } from 'react';
import {AuthContext} from '../context/auth';
import * as localService from '../../services/localStorage';
import { Link } from 'react-router-dom';
import FastAverageColor from 'fast-average-color';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHeart ,faEye, faEyeSlash} from '@fortawesome/free-solid-svg-icons';
import { faHeart as reHeart}  from '@fortawesome/free-regular-svg-icons';
import { SetFavourite,DeleteFavourite,SetWatch,DeleteWatch } from '../../services/firebase';
//import { CSSTransition } from 'react-transition-group';
import {FavouriteContext} from '../context/favourite';

const MainList = ({promise,resource = null,title = false,history}) =>{
    resource = promise ? promise.read() : resource;

    if(resource.length !== 0){return(
        <div className="main-list">
            <h3 className="main-list__title">{title}</h3>
            <div className="main-list__grid">
                {resource.map((item)=><CardDominant key={item.id} item={item}/>)}
            </div>
        </div>
    )}else return false;
};

const CardDominant = ({item}) =>{
    const {currentUser} = useContext(AuthContext);
    const[hover,setHover] = useState(false);
    const[dominantColor,setDominantColor] = useState('');
    const toggleHover = () => {
        setHover(!hover);
    };
    useEffect(()=>{
        const fac = new FastAverageColor();
        if(item.poster !== null){
            const img = new Image();
            img.crossOrigin = "anonymous";
            img.src = item.poster;
            fac.getColorAsync(img, {
                ignoredColor: [0, 0, 0, 255] // black
            }).then(function(color) {
                setDominantColor(`0 0 5rem 0.4rem ${color.rgba}`);
            });
        };
        return function cleanup(){
            fac.destroy();
        };
    },[item]);
    const hoverStyle = hover ? {boxShadow: dominantColor,borderRadius: '4%'} : {};

    return (
        <div className="main-list__card" style={hoverStyle}
        onMouseEnter={toggleHover}
        onMouseLeave={toggleHover}>
            <Link className="main-list__link"
                to={`/${item.type}/${item.id}`}
                onClick={()=>localService.addLatest(item)}/>
            <div className="main-list__shadow"/>
            {currentUser ? 
                    <React.Fragment>
                        <FavouriteIcon item={item}/>
                        <WatchIcon item={item}/>
                    </React.Fragment>
                 : false}
            <img className="main-list__poster" alt="" src={item.poster}/>
            <p className="main-list__name">{item.name}</p>
        </div>
    )
}

const FavouriteIcon = ({item}) =>{
    const {currentUser} = useContext(AuthContext);
    const {fav,setFav} = useContext(FavouriteContext);
    const delFavourite = fav.filter(x=>x.id!==item.id);
    const addFavourite = [...fav].concat(item);
    return(
        <div className="main-list__favourite">
            {fav.find(x=>x.id===item.id) ? 
                <FontAwesomeIcon icon={faHeart} className="main-list__favIcon"
                color="pink"
                onClick={()=>{
                    DeleteFavourite(currentUser.uid,item);
                    setFav(delFavourite);
                }}/> 
            :   
            <FontAwesomeIcon icon={reHeart} className="main-list__favIcon"
            color="pink"
            onClick={()=>{
                SetFavourite(currentUser.uid,item);
                setFav(addFavourite);
            }}/>
            }
        </div>
    )
}

const WatchIcon = ({item}) =>{
    const {currentUser} = useContext(AuthContext);
    const {watch,setWatch} = useContext(FavouriteContext);
    const watchExist = watch ? watch.find(x=>x.id===item.id) : null;
    const delWatch = watch ? watch.filter(x=>x.id!==item.id) : null;
    const addWatch = watch ? [...watch].concat(item) : null;
    return(
        <div className="main-list__watchlist">
            {watchExist ? 
                <FontAwesomeIcon icon={faEyeSlash} className="main-list__watchIcon"
                color="pink"
                onClick={()=>{
                    DeleteWatch(currentUser.uid,item);
                    setWatch(delWatch);
                }}/> 
            :   
            <FontAwesomeIcon icon={faEye} className="main-list__watchIcon"
            color="pink"
            onClick={()=>{
                SetWatch(currentUser.uid,item);
                setWatch(addWatch);
            }}/>
            }
        </div>
    )
}

