import React from 'react';
import NavAuth from './nav-auth';
import {NavLink} from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faHeart,faSearch,faHome,faEye } from '@fortawesome/free-solid-svg-icons'
import './nav.scss';

const Nav = () =>{
    return(
        <nav className="nav container">
            <ul className="nav__list">
                <li className="nav__item">
                    <ShorterLink to={'/'}>
                    <FontAwesomeIcon className="nav__icon" icon={faHome}/>
                    </ShorterLink>
                </li>
                <li className="nav__item">
                    <ShorterLink to={'/search'} exact={false}>
                        <FontAwesomeIcon className="nav__icon" icon={faSearch}/>
                    </ShorterLink>
                </li>
            </ul>
            <ul className="nav__list link-inverse">
                <NavAuth>
                    <li className="nav__item">
                        <ShorterLink to={'/favourite'} reverse={false}>
                            <FontAwesomeIcon icon={faHeart} className="nav__icon"/>
                        </ShorterLink>
                    </li>
                    <li className="nav__item">
                        <ShorterLink to={'/watchlist'} reverse={false}>
                            <FontAwesomeIcon icon={faEye} className="nav__icon"/>Watchlist
                        </ShorterLink>
                    </li>
                </NavAuth>
            </ul>
        </nav>
    );
};

const ShorterLink = ({to,exact = true,reverse = true,...props}) =>{
    const autoReverse = reverse ? "nav__link--active" : "nav__link--active_reverse";
    return (
        <NavLink className="nav__link"
            activeClassName={autoReverse} exact={exact}
            to={to}>{props.children}
        </NavLink>
    )
}

export default Nav;