import React, { useState } from 'react';
import * as localService from '../../services/localStorage';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLongArrowAltRight } from '@fortawesome/free-solid-svg-icons';

const ContinueList = ({promise,resource = null,title,history}) =>{
    resource = promise ? promise.read() : resource;
    const[open,setOpen] = useState(false);
    const[seeAllText,setSeeAllText] = useState(false);
    const handleOpen = () =>{
        setOpen(!open);
        setSeeAllText(!seeAllText);
    };
    const text__seeAll = seeAllText ? '  ' : '  ';

    return(
        <div className="continue-list__block">
            <div className="continue-list__header">
                <h4 className="continue-list__subTitle">{title}</h4>
                {resource.length > 5 ?
                    <p className="continue-list__seeAll"
                    onClick={handleOpen}>
                    {text__seeAll}<FontAwesomeIcon icon={faLongArrowAltRight}/>
                    </p> 
                : false}
            </div>
            <div className="continue-list__grid">
                {resource.slice(0,5).map((item)=>(
                    <Item item={item} key={item.id}/>
                ))}
                {open && resource.slice(5).map((item)=>(
                    <Item item={item} key={item.id}/>
                ))}
            </div>
        </div>
    );
};

const Item = ({item}) =>{
    const gradientWidth = parseInt(item.time / item.endTime * 100);
    return(
        <div className="continue-list__card">
            <Link className="continue-list__link" to={{
                pathname: `/${item.type}/${item.id}`,
                state: {
                    item
                }
            }} onClick={()=>localService.addLatest(item)}/>
            <img className="continue-list__poster" alt="" src={item.poster}/>
            <div className="continue-list__info">
                <p className="continue-list__name">{item.name}</p>
                {<InfoTV item={item}/>}
                <div className="continue-list__status">
                    <span className="continue-list__gradient" 
                    style={{width: `${gradientWidth}%`}}/>
                </div>
            </div>
        </div>
    )
}

const InfoTV = ({item}) =>{
    if(item.season)return(
        <div className="continue-list__tv">
            <p className="continue-list__subtext"> {item.season}</p>
            <p className="continue-list__subtext"> {item.episode}</p>
        </div>
    ); return false
}

export default ContinueList;