import React,{useEffect,useState,useContext} from 'react';
import {GetAllFavoutiresTest,GetWatchlistTest} from '../../services/firebase';
import {AuthContext} from './auth';

const FavouriteContext = React.createContext();

const FavouriteProvider = ({children}) =>{
    const {currentUser} = useContext(AuthContext);
    const[fav,setFav] = useState(null);
    const[watch,setWatch] = useState(null);
    useEffect(()=>{
        let unmounted = false;
        if(currentUser){
            const fetchOne = GetAllFavoutiresTest(currentUser.uid);
            const fetchTwo = GetWatchlistTest(currentUser.uid);
            Promise.all([fetchOne,fetchTwo])
                .then(data=>{if(!unmounted){setFav(data[0]);setWatch(data[1])}});
        }return () => { unmounted = true };
    },[currentUser]);
    if(fav){
        return (<FavouriteContext.Provider
            value={{
                fav,setFav,
                watch,setWatch
            }}
        >{children}
        </FavouriteContext.Provider>)
    }
    if(!currentUser){
        return (<FavouriteContext.Provider
            value={{
                fav,setFav,
                watch,setWatch
            }}
        >{children}
        </FavouriteContext.Provider>)
    }
    return false
};

export{
    FavouriteProvider,
    FavouriteContext
}