import React,{useContext} from 'react';
import ContinueList from './main-continue__list';
import {AuthContext} from '../context/auth';

const MainContinue = () =>{
    const {continueS} = useContext(AuthContext);
    if(continueS[0].length || continueS[1].length)return(
        <div className="continue-list">
            <h3 className="continue-list__title"></h3> 
            <TV context={continueS}/>
            <Movie context={continueS}/>
        </div>
    );return false;
};

const TV = ({context}) =>{
    if(context[0].length)return<ContinueList title={''} resource={context[0]}/>
    return false;
}

const Movie = ({context}) =>{
    if(context[1].length)return<ContinueList title={''} resource={context[1]}/>
    return false;
}

export default MainContinue;