const GoogleSignUp = () => {
    firebase.auth().signInWithPopup(provider);
};
const SignOut = () => {
    app.auth().signOut();
};

const GetAllFavoutiresTest = async (uid) =>{
    const promise = await db.collection('continueData').doc(uid).collection('favourite').get()
        .then(data =>
            data.docs.map(doc => doc.data())
        );
    return promise;
}

const DeleteFavourite = (uid,item) =>{
    const { id } = item;
    return db.collection('continueData').doc(uid).collection('favourite').doc(id).delete()
};

const SetFavourite = (uid,item) =>{
    const { id,type,name,poster } = item;
    // const userId = firebase.auth().currentUser.uid;
    return db.collection('continueData').doc(uid).collection('favourite').doc(id).set({
        id,
        type,
        name,
        poster,
        date: firebase.firestore.Timestamp.now()
    });
};

