const Favourite = (uid,id,handleFavourite) =>{
    const data = db.collection('continueData').doc(uid).collection('favourite').doc(id).get()
    .then(data => {
        data.data();
        if(data.data() !== undefined){
            handleFavourite();
        };
        })
    return data;
};

const GetContinueTV = (uid,id) =>{
    const promise = db.collection('continueData').doc(uid).collection('tv').doc(id).get()
        .then(data => {
            if(data.data()){
                return data.data()
            }else{
                return null;
            }})
    return wrapPromise(promise);
}

