import React, { useContext, useState } from 'react';
import {AuthContext} from '../context/auth';
import {GoogleSignUp,SignOut} from '../../services/firebase';
import googleLogo from './google.svg';

const NavAuth = (props) =>{
    const {currentUser} = useContext(AuthContext);
    return(
        currentUser ? <LoggedIn props={props.children}/> : 
        <li className="nav__item">
            <img src={googleLogo} alt='' className='nav__auth' 
            onClick={() => GoogleSignUp()}/>
        </li>
    )
};

const LoggedIn = ({props}) =>{
    const {currentUser} = useContext(AuthContext);
    const firstName = currentUser.displayName.split(" ")[0];
    return(
        <React.Fragment>
            {props}
            <NavItem icon={currentUser.photoURL} title={firstName}>
                <NavItemDropdown/>
            </NavItem>
        </React.Fragment>
    );
};

const NavItem = ({icon,title,...props}) =>{
    const[open,setOpen] = useState(false);
    const[active,setActive] = useState(false);
    const handleClick = () =>{
        setOpen(!open);
        setActive(!active);
    }
    return(
        <li className="nav__item" onClick={handleClick}>
            <img className="nav__auth" alt='' src={icon}/>
            <span className="nav__name">{title}</span>
            {open && props.children}
        </li>
    );
};

const NavItemDropdown = () =>{
    return(
        <div className="nav__dropdown">
            <span onClick={()=>SignOut()}></span>
        </div>
    );
};

export default NavAuth;