import React,{useEffect,useState} from 'react';
import {app,GetTvTimeTest,GetMovieTimeTest} from '../../services/firebase';
import {FullScreen} from '../loading';
import * as movieService from '../../services/theMovieDB';

const AuthContext = React.createContext();

const AuthProvider = ({children}) =>{
    const [currentUser,setCurrentUser] = useState(null);
    const [authIsReady,setAuthIsReady] = useState(false);
    const [continueData,setContinueData] = useState(null);
    useEffect(()=>{
        app.auth().onAuthStateChanged((user)=>{
            setCurrentUser(user);
            setAuthIsReady(true);
        })
    },[]);
    if(authIsReady && !continueData){
        const fetchOne = GetTvTimeTest(currentUser.uid);
        const fetchTwo = GetMovieTimeTest(currentUser.uid);
        const fetchThree = movieService.findTrendTV();
        Promise.all([fetchOne,fetchTwo,fetchThree]).then(data=>setContinueData(data));
    }
    if(currentUser && continueData){
        return(
            <AuthContext.Provider
                value={{
                    currentUser,
                    continueS: continueData
                }}
            >{children}
            </AuthContext.Provider>
        )
    }
    if(!currentUser && continueData){
        return(
            <AuthContext.Provider
                value={{
                    currentUser,
                    continueS: []
                }}
            >{children}
            </AuthContext.Provider>
        )
    }
    return <FullScreen/>;
};

export{
    AuthProvider,
    AuthContext
}